@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
*, *::after, *::before {
	 margin: 0;
	 padding: 0;
	 box-sizing: inherit;
}

:focus {
  outline: none;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: -webkit-linear-gradient(40deg, #69f, #96f);
  background: linear-gradient(50deg, #69f, #96f);
  font-family: 'Lato', sans-serif;
}

.scroll-style::-webkit-scrollbar-track {
  background-color: transparent;
  box-shadow: inset 0 0 0.6rem rgba(0, 0, 0, 0.3);
}
.scroll-style::-webkit-scrollbar {
  background-color: transparent;
  width: 0.75rem;
}
.scroll-style::-webkit-scrollbar-thumb {
  background-color: #69f;
  box-shadow: inset 0 0 0.6rem rgba(0, 0, 0, 0.3);
}
.scroll-style {
  scrollbar-color: #69f transparent;
  scrollbar-width: thin;
}

input[type="text"] {
  width: 100%; 
  box-sizing: border-box;
  padding: 3px 7px 4px;
}

.button {
  box-shadow: rgb(100, 65, 164) 0px 34px 0px -15px inset;
  background-color: rgb(100, 65, 164);
  border: none;
  display: inline-block;
  cursor: pointer;
  color: rgb(255, 255, 255);
  font-weight: bold;
  padding: 3px 16px;
  text-decoration: none;
  text-shadow: rgb(100, 20, 166) 0px -1px 0px;
  margin: auto 0.5rem;
  max-height: 26px;
}

@media only screen and (max-width: 37.5em) {
 html {
  font-size: 55%;
}
}

@media only screen and (max-width: 56.25em) {
  html {
    font-size: 60%;
 }
}

.messages-container {
    overflow-y: scroll;
    word-wrap: break-word;
    overflow-x: hidden;
    height: 100%;
}
  
.message {
    word-break: break-all;
    padding: .6rem 1rem 1rem;
    font-size: 1.5rem;
    font-family: 'Lato', sans-serif;
}
  
.message:nth-of-type(2n) {
    background-color: #DDD;
}
  
.message-username {
    color: #6699FF;
    font-weight: 700;
    margin: 0 .3rem 1rem 0;
}
  
.message-author {
    color: #7d5bbe;
    text-decoration: underline;
    font-weight: 700;
    margin: 0 .3rem 1rem 0;
}
  
.message-time {
    font-size: 1.2rem;
    font-weight: bold;
    font-family: "Helvetica Neue", "Helvetica", sans-serif;
}
  
.message-text {
    color: #1A2A3A;
}
  
div.message.no-send {
    background-color: #e57373;
}

.chat-actions {
    margin-bottom: 1px;
}

.chat-container {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    border-radius: 0.1rem;
    box-shadow: 0.5rem 0.5rem 1rem rgba(0, 0, 0, .5);
    height: 60rem;
    width: 50rem;
    overflow: hidden;
}

@media only screen and (max-width: 56.25em) {
    .chat-container {
      height: 85vh;
      width: 60vw;
   }
   .chat-textarea {
    border: none;
    border-top: 0.1rem solid #69f;
    border-radius: 0;
    font-size: 2rem;
    height: 5.8rem;
    padding-right: 11.75rem;
    width: 100%;
  }
}

@media only screen and (max-width: 37.5em) {
    .chat-container {
      height: 100vh;
      width: 100vw;
   }
  }

.change-name {
    display: flex;
    flex-direction: row;
}
  
.change-name span {
    margin: auto 0;
}
  
.change-name div {
    position: relative;
    margin: auto 0.5rem;
}

  
.effect-1 {
    border: 0; 
    padding: 7px 0; 
    border-bottom: 1px solid #ccc;
}
  
.effect-1 ~ .focus-border {
    position: absolute; 
    bottom: 0; 
    left: 0; 
    width: 0; 
    height: 2px; 
    background-color: #7d5bbe; 
    -webkit-transition: 0.4s; 
    transition: 0.4s;
}
  
.effect-1:focus ~ .focus-border {
    width: 100%; 
    -webkit-transition: 0.4s; 
    transition: 0.4s;
}

@media only screen and (max-width: 25.25em) {
    .change-name {
      flex-direction: column;
    }
}
  

.header-top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0.5rem 1rem;
    background-color: #7d5bbe;
}

.header div{
    position: relative;
}

.header-bottom {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    margin: 0 1rem;
    color: #1A2A3A;
}

.header-buttons {
    display: flex;
    flex-direction: row;
}

.connection-status {
    background-color: transparent;
    margin: 0 1rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    font-size: 1.5rem;
}

.chat-status {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
}

.pulse {
    -webkit-animation-name: pulse;
    animation-name: pulse;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite;
    }
    @-webkit-keyframes pulse {
    0% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
    }
    50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
    }
    100% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
    }
    }
    @keyframes pulse {
    0% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
    }
    50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
    }
    100% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
    }
}

.connected {
    background-color: #b7dfb8;
}
  
.disconnected {
    background-color: #f6a6c1;
}
  
.reconnection {
    background-color: #8995d6;
}

.chat-header {
    display: flex;
    flex-direction: column;
    border: none;
    border-bottom: 0.1rem solid #69f;
    border-radius: 0;
    font-size: 1.8rem;
    height: 12rem;
    min-height: 10rem;
    width: 100%;
    color: #fff;
}
  
.chat-header h1 {
    text-transform: uppercase;
    font-size: 1.8rem;
    letter-spacing: 0.1rem;
}

@media only screen and (max-width: 25.25em) {
    .chat-header {
      font-size: 1.6rem;
      height: 25rem;
    }
    .chat-status {
      flex-direction: column;
      align-items: center;
    }
}
  
  

.chat-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.chat-textarea {
    background-color: transparent;
    font-family: 'Lato', sans-serif;
    font-size: 1.5rem;
    outline: none;
    resize: none;
    padding-right: 11.75rem;
    width: 100%;
    height: 5.8rem;
    padding: 0.5rem 11.75rem 0.5rem 1rem ;
}


.chat-buttons {
    display: inline-flex;
    align-items: center;
    position: absolute;
    right: 0.75rem;
    top: 0;
    padding: 0;
    height: 5rem;
    width: auto;
    width: initial;
  }
  
  .chat-button--submit {
    background: #fff;
    border: 0.2rem solid #6699FF;
    border-radius: .4rem;
    color: #6699FF;
    font-size: 2rem;
    letter-spacing: .2rem;
    margin-top: 1rem;
    margin-right: 1rem;
    outline: none;
    padding: .8rem 1rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
  }

  @media only screen and (max-width: 56.25em) {
   .chat-form {
    padding-top: 0;
  }
}
  

